* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
        Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    background-color: #eff3f7;
    height: 100%;
}


.container {

    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 100px;
}

.movie {
    padding: 15px;
    display: flex;
    width: 40%;
    background-color: #fff;
    margin-right: 100px;
    margin-bottom: 80px;
    border-radius: 5px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
        0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie img {

    position: relative;
    top: -50px;
    width: 100%;
    max-width: 150px;
    height: 200px;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
        0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie_data .movie_title,
.movie_data .movie_year {
    margin: 0;
    font-weight: 300;
}

.movie_data .movie_title {
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
}

.movie .movie_data .movie_genres {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    margin: 5px 0px;
}

.movie_genres li,
.movie .movie_year {
    margin-right: 10px;
    font-size: 14px;
}

